// Typogoraphy
$h1-font-size: 55px;
$h2-font-size: 35px;

p {
    font-size: 25px;
}
// Colors
$clutch-purple-dark: #2f233b;
$clutch-purple: #2f1d57;
$clutch-orange: #f16148;
$clutch-purple-light: #b2a2c0;
$clutch-gray: #77727c;
$ease: 0.3s ease all;
@mixin header-font {
    font-family: "miller-display", serif;
}
@mixin body-font {
    font-family: 'montserrat', sans-serif;
}
@mixin footer-text {
    font-family: 'montserrat', sans-serif;
    color: $dark-blue;
}
@mixin admin-sticky-fix($offset: 0) {
    $narrow-offset: 46px;
    $wide-offset: 32px;

    @if $offset != 0 and type-of($offset) == "number" {
        $narrow-offset: $narrow-offset + $offset;
        $wide-offset: $wide-offset + $offset;
    }

    .admin-bar & {
        top: $narrow-offset;
        @media screen and (min-width: 783px) {
            top: $wide-offset;
        }
    }
}